body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}


.offset-top {
  position: relative;
}

/* Palette generated by Material Palette - materialpalette.com/cyan/amber */

.dark-primary-color {
    background: #0097A7;
}

.default-primary-color {
    background: #00BCD4;
}

.light-primary-color {
    background: #B2EBF2;
}

.text-primary-color {
    color: #FFFFFF;
}

.accent-color {
    background: #FFC107;
}

.primary-text-color {
    color: #212121;
}

.secondary-text-color {
    color: #757575;
}

.divider-color {
    border-color: #BDBDBD;
}
.App {
  text-align: center;
}

.main {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

.main {
  flex: 1 0 auto;
}
.offset-top {
  margin-top: -36px;
}
.container.margin-top {
  margin-top: 6em;
}

.no-margin {
  margin: 0 !important;
}
.modal {
    z-index: 1003;
    display: block; 
    opacity: 1; 
    bottom: 0px;
}
.meal {
    display: flex;
    justify-content: space-around;
}

.add-meal-btn {
    position: relative;
    margin-top: -9%;
    margin-left: 80%;
}

.tabs-content .carousel-item {
    height: auto;
}
.bullet-separator {
    width: 8px;
    height: 8px;
    background: #dadada;
    display: inline-block;
    border-radius: 50%;
}
.top-dashboard {
    width: 100%;
    height: 120px;
    background: purple;
}

.top-dashboard p {
    margin-top: 0;
}
.card-menu {
    background-repeat: no-repeat;
    background-size: 100%;
    background-blend-mode: overlay;
}

.bg-workout {
    background-image: url(/workout-bg.jpg);
}

.diet-bg {
    background-image: url(/diet-bg.jpg);
}

.nutrition-bg {
    background-image: url(/nutrition-bg.jpg);
}

.tips-bg {
    background-image: url(/tips-bg.jpg);
}

.black-trans {
    background-color: #00000096 !important;
}
.strip {
    margin-bottom: 5%;
}
.navbar ul a :not(.side-nav) {
    color: #fff;
    background: transparent;

}

.sidenav {
    color: black;
}

