    color: #fff !important;
    background: #00BCD4 !important;
} */
nav {
    position: fixed;
    z-index: 1000;
}

nav a,
nav .brand-logo {
    color: black;
}

nav .sidenav-trigger {
    padding-right: 20px;
    border-right: 1px solid #ddd;
}

.logo {
    width: 101px;
    margin-right: 50.5px;
}

.home-sidenav {
    display: flex !important;
}

.home-sidenav i {
    margin: auto !important;
}

.follow-us-icons {
  display: -webkit-inline-box;
}

.follow-us-icons>li>img {
  padding: 10px;
}
